.Splash {
  position: fixed;
  left: 16px;
  right: 16px;
  top: 16px;
  bottom: 16px;
  border-radius: 3px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: white;
  z-index: 1;
  border: solid 1px #e5e5e5;
}

.Progress {
  width: 40vw;
  height: 0.5rem;
  border: solid 1px #004091;
  border-radius: 3px;
  overflow: hidden;
  margin: auto;
}

.ProgressBar {
  background-color: #004091;
  height: 100%;
}

.Loading {
  text-align: center;
  position: absolute;
  top: 1rem;
  left: 0;
  right: 0;
  margin: auto;
  -webkit-transition: opacity 1s;
  -o-transition: opacity 1s;
  transition: opacity 1s;
  font-size: 14px;
  color: #004091;
}



