.Score {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  background-color: rgba(20, 20, 20, 0.95);
  overflow-x: auto;
}
.Score,
.PlayPause {
  -webkit-transition: bottom 0.25s;
  -o-transition: bottom 0.25s;
  transition: bottom 0.25s;
}

.ScoreWrapper {
  background-color: white;
  margin: 1.5rem 0 0 1.5rem;
  padding: 0 18px;
  height: 15rem;
  overflow: hidden;
  border-radius: 3px;
  border: solid #e5e5e5 1px;
  position: relative;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.ShowHideScore {
  position: fixed;
  right: 1.5rem;
  bottom: 175px;
  height: 3rem;
  font-size: 2rem;
  line-height: 1.5;
  border-top-right-radius: 3px;
  border-top-left-radius: 3px;
  background-color: rgba(20, 20, 20, 0.95);
  text-align: center;
  color: white;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: bottom 0.25s;
  -o-transition: bottom 0.25s;
  transition: bottom 0.25s;
  padding: 0 1.5rem 0 1.25rem;
}

.AccordeonArrow {
  vertical-align: -0.45rem;
  display: inline-block;
}

.ScoreLength {
  font-size: 14px;
  padding-right: 6px;
  width: 3rem;
  vertical-align: 1px;
}

.PlayPause {
  position: fixed;
  left: calc(50vw - 2rem);
  height: 4rem;
  width: 4rem;
  border-radius: 2rem;
  line-height: 3.5rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  text-align: center;
  color: white;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.PlayPause:hover,
.PlayPause:active {
  background-color: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.1);
}



